@charset "UTF-8";
@font-face {
  font-weight: normal;
  font-family: "TT Lakes";
  font-style: normal;
  src: url("../fonts/TTLakes-Regular.woff") format("woff"), url("../fonts/TTLakes-Regular.woff2") format("woff2"); }

@font-face {
  font-weight: bold;
  font-family: "TT Lakes";
  font-style: normal;
  src: url("../fonts/TTLakes-Bold.woff") format("woff"), url("../fonts/TTLakes-Bold.woff2") format("woff2"); }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  font-family: "TT Lakes", "Arial", sans-serif;
  color: #1b1c21; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (min-width: 1336px) {
  .page-wrapper {
    display: flex; } }

.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 290px;
  height: 88px;
  margin: 0 auto;
  padding: 0px 15px; }

@media (min-width: 768px) {
  .header {
    width: 718px;
    padding: 0 25px; } }

@media (min-width: 1336px) {
  .header {
    width: 320px; } }

.header__logo {
  position: absolute;
  top: 20%;
  left: 25%;
  z-index: 1; }

@media (min-width: 768px) {
  .header__logo {
    left: 15%; } }

@media (min-width: 1336px) {
  .header__logo {
    top: 22%;
    left: 7%; } }

.header__nav-buttons-wrapper {
  width: 50%; }
  .header__nav-buttons-wrapper--open {
    position: absolute;
    top: 24px;
    width: 290px;
    height: 100vh;
    background-color: #ffffff; }

@media (min-width: 768px) {
  .header__nav-buttons-wrapper {
    width: 295px; } }

@media (min-width: 1336px) {
  .header__nav-buttons-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 272px;
    height: 100vh; } }

.header__nav-buttons-centric {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%; }

.header__nav-toggle-burger--open {
  display: none; }

@media (min-width: 1336px) {
  .header__nav-toggle-burger {
    display: none; } }

.header__nav-toggle-cross {
  display: none; }
  .header__nav-toggle-cross--open {
    display: flex; }

@media (min-width: 1336px) {
  .header__nav-toggle-cross {
    display: none; } }

.header__search-button {
  display: none; }
  .header__search-button--open {
    display: flex; }

@media (min-width: 1336px) {
  .header__search-button {
    display: flex;
    margin-left: auto;
    margin-top: 25px; } }

.header__contact-list {
  display: none;
  /*&--open {
    position: absolute;
    top: 460px;
    z-index: 2;
  }*/ }

@media (min-width: 768px) {
  .header__contact-list {
    position: absolute;
    top: 8px;
    right: 170px;
    display: flex;
    justify-content: space-between;
    width: 150px; }
    .header__contact-list--open {
      display: none;
      /*временно*/ } }

@media (min-width: 1336px) {
  .header__contact-list {
    display: none;
    /*временно*/ } }

.header__service-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  margin-top: 7px;
  padding: 0;
  width: 95px; }
  .header__service-list--open {
    display: none; }

@media (min-width: 768px) {
  .header__service-list--open {
    display: flex;
    margin-left: auto; } }

@media (min-width: 1336px) {
  .header__service-list {
    display: none; } }

.header__navigation {
  display: none; }
  .header__navigation--open {
    display: flex;
    position: absolute;
    top: 64px;
    left: 0;
    background-color: #ffffff; }

@media (min-width: 1336px) {
  .header__navigation {
    position: absolute;
    top: 88px;
    left: 15px; } }

.header__bottom-wrapper {
  display: none; }

.header__phone,
.header__email {
  margin: 0;
  padding: 0; }

.header__email {
  margin-bottom: 10px; }

.header__phone {
  font-size: 24px;
  line-height: 32px;
  font-weight: bold; }

/*
.header__navigation {

  background-color: #ffffff;
}*/
.button {
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  border-radius: 50%; }

.button__icon:hover,
.button__icon:hover,
.button__icon:hover {
  opacity: 0.8; }

.button__icon:focus,
.button__icon:focus,
.button__icon:focus {
  opacity: 0.5; }

@media (min-width: 1336px) {
  .navigation {
    display: flex; } }

.navigation__site-list {
  margin: 0;
  padding: 0; }

@media (min-width: 768px) {
  .navigation__site-list {
    margin-top: 30px; } }

.site-list {
  display: flex;
  flex-direction: column;
  list-style: none; }

.site-list__link {
  position: relative;
  display: inline-block;
  padding: 10px 0;
  text-decoration: none;
  color: #1b1c21; }
  .site-list__link--active {
    color: #7e7e82; }
  .site-list__link--active::before {
    content: "";
    position: absolute;
    top: 7%;
    left: -11%;
    width: 4px;
    height: 32px;
    background-color: #41f6d7;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px; }

@media (min-width: 768px) {
  .site-list__link--active::before {
    left: -19%; } }

.service-list {
  list-style: none; }

.service-list--main {
  display: none; }

@media (min-width: 1336px) {
  .service-list--main {
    display: flex;
    flex-wrap: wrap;
    width: 370px; } }

@media (min-width: 1336px) {
  .service-list--header {
    display: none; } }

.service-list__link {
  color: inherit;
  text-decoration: none; }

@media (min-width: 1336px) {
  .service-list__link {
    display: flex;
    align-items: center; } }

.service-list__icon:hover {
  opacity: 0.8; }

.service-list__icon:active {
  opacity: 0.5; }

@media (min-width: 1336px) {
  .service-list__icon {
    margin-left: 8px; } }

.contact-list {
  list-style: none; }

.contact-list__link {
  text-decoration: none; }

.contact-list__icon:hover {
  opacity: 0.8; }

.contact-list__icon:active {
  opacity: 0.5; }

.toggle-language-list {
  list-style: none; }

.toggle-language-list__btn {
  margin: 0;
  margin-right: 5px;
  padding: 0;
  font-size: inherit;
  text-transform: uppercase;
  background-color: transparent;
  border: none; }
  .toggle-language-list__btn--active {
    color: #7e7e82; }

.main {
  background-color: #f8f8f8; }

.main__header {
  width: 290px;
  margin: 0 auto;
  margin-bottom: 24px;
  padding: 25px 15px 0; }

@media (min-width: 768px) {
  .main__header {
    width: 718px;
    margin-bottom: 32px;
    padding-right: 25px;
    padding-left: 25px; } }

@media (min-width: 1336px) {
  .main__header {
    display: flex;
    justify-content: space-between;
    width: 996px;
    margin-bottom: 8px;
    padding: 42px 35px 0; } }

.main__title {
  margin: 0;
  font-size: 28px;
  font-weight: bold;
  line-height: 40px; }

.services {
  min-height: 442px;
  /*временно*/ }

@media (min-width: 768px) {
  .services {
    min-height: 435px;
    /*временно*/ } }

@media (min-width: 1336px) {
  .services {
    min-height: 565px;
    /*временно*/ } }

.services__wrapper {
  width: 290px;
  margin: 0 auto 24px;
  padding: 0 15px; }

@media (min-width: 768px) {
  .services__wrapper {
    width: 718px;
    padding: 0 25px;
    margin-bottom: 32px; } }

@media (min-width: 1336px) {
  .services__wrapper {
    width: 996px;
    padding-right: 35px;
    padding-left: 35px;
    margin-bottom: 19px; } }

.page-navigation {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none; }
  .page-navigation::-webkit-scrollbar {
    width: 0;
    background-color: transparent; }

@media (min-width: 1336px) {
