    overflow: hidden; } }

.page-navigation__item {
  display: flex;
  padding: 8px 21px; }

.page-navigation__item--active {
  border: 2px solid #b8ffec;
  background-color: #ffffff; }

.page-navigation__link {
  color: #7e7e82;
  text-decoration: none;
  white-space: nowrap; }

.about-us__wrapper {
  width: 290px;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px; }

@media (min-width: 768px) {
  .about-us__wrapper {
    display: flex;
    justify-content: space-between;
    width: 718px;
    padding-right: 25px;
    padding-left: 25px; } }

@media (min-width: 1336px) {
  .about-us__wrapper {
