@media (min-width: 1336px) {
  .about-us__picture {
    width: 532px;
    height: 307px; } }

.more-button {
  display: flex;
  margin-bottom: 15px;
  padding: 0;
  padding-left: 5px;
  font: inherit;
  border: none;
  background-color: transparent; }

@media (min-width: 768px) {
  .more-button {
    margin-bottom: 0; } }

.more-button__icon {
  margin-right: 5px; }
